import React, { memo, useEffect } from "react";
import classNames from "classnames";
import { shallowEqual, useDispatch, useSelector } from "react-redux";

import {
  HomeContentEventLoadMoreWrapper,
  HomeContentLayoutModeWrapper,
  HomeContentWrapper,
} from "./style";
import KTEvemtsListView from "@/components/events-list-view";
import {
  getChangeHomeLayoutModeAction,
  getHomeEventsAction,
} from "../../store/actionCreators";
import {
  LAYOUT_MODE_CARD,
  LAYOUT_MODE_LIST,
} from "../../../../common/constants";
import {
  createLayoutModeCardSvg,
  createLayoutModeListSvg,
} from "../../../../common/icon-svg";

const KTHomeContent = memo(() => {
  const { layoutMode, events = [] } = useSelector(
    (state) => ({
      layoutMode: state.getIn(["home", "layoutMode"]),
      events: state.getIn(["home", "events"]),
    }),
    shallowEqual
  );

  const dispatch = useDispatch();
  // useEffect(() => {
  //   dispatch(getHomeEventsAction());
  // }, [dispatch]);

  const onclickChangeLayoutMode = (mode) => {
    dispatch(getChangeHomeLayoutModeAction(mode));
  };

  const onclickLoadMoreButton = () => {
    dispatch(getHomeEventsAction());
  };

  return (
    <HomeContentWrapper>
      <HomeContentLayoutModeWrapper>
        {createLayoutModeCardSvg(
          classNames("layoutModel", {
            layoutModelActive: layoutMode === LAYOUT_MODE_CARD,
          }),
          () => onclickChangeLayoutMode(LAYOUT_MODE_CARD)
        )}
        {createLayoutModeListSvg(
          classNames("layoutModel", {
            layoutModelActive: layoutMode === LAYOUT_MODE_LIST,
          }),
          () => onclickChangeLayoutMode(LAYOUT_MODE_LIST)
        )}
      </HomeContentLayoutModeWrapper>
      <KTEvemtsListView
        key={layoutMode}
        events={events}
        layoutMode={layoutMode}
      />
      <HomeContentEventLoadMoreWrapper>
        <div className="button" onClick={(item) => onclickLoadMoreButton()}>
          Load More
        </div>
      </HomeContentEventLoadMoreWrapper>
    </HomeContentWrapper>
  );
});

export default KTHomeContent;
