import React, { memo } from "react";
import { ExpandableButtonWapper } from "./style";
import { arrowSvg } from "../../common/icon-svg";

const KTExpandableButton = memo((props) => {
  const { title, onClick } = props;
  return (
    <ExpandableButtonWapper onClick={onClick}>
      <div className="title">{title}</div>
      {arrowSvg({
        width: "7px",
        height: "4px",
        fill: "#4D4D4D",
        className: "arrow",
      })}
    </ExpandableButtonWapper>
  );
});

export default KTExpandableButton;
