import React, { PureComponent } from "react";
import { FooterTopListWrapper } from "./style";

export default class KTFooterTopList extends PureComponent {
  render() {
    const { title, links } = this.props;
    const items = links.map(item => {
      return <div key={item.title} className="item">{item.title}</div>;
    });

    return (
      <FooterTopListWrapper>
        <div className="title">{title}</div>
        {items}
      </FooterTopListWrapper>
    );
  }
}
