import React, { PureComponent } from "react";
import {
  ActivityCardContent,
  EventCardWrapper,
  CardContentBuyTicketButton,
  CardContentDateTimeBar,
  CardContentOptionBar,
  CardContentTitleBar,
} from "./style";
import { addressSvg, likeSvg, shareSvg } from "../../common/icon-svg";

export default class KTEventCard extends PureComponent {
  constructor(props) {
    super(props);

    this.state = {
      isOnMouseOver: false,
      isLike: false,
    };
  }

  render() {
    const { event_title, addr1, eventposter } = this.props.item;

    const eventposter2 = "bg-eventlist01.png";
    const { isOnMouseOver, isLike } = this.state;

    const dateStr = "08/23/2023";
    const timeStr = "6:30 pm, Wed.";
    return (
      <EventCardWrapper>
        <div className="header">
          {addressSvg({
            className: "headerIcon",
            width: "22px",
            height: "22px",
            stroke: "#4D4D4D",
            strokeWidth: "1.5",
          })}
          <div className="headerTitle">{addr1}</div>
        </div>
        <ActivityCardContent
          onMouseOver={(item) => this.onMouseOver(item)}
          onMouseOut={(item) => this.onMouseOut(item)}
          style={{
            backgroundImage:
              "url(" +
              require(`../../assets/img/event-list/${eventposter2}`) +
              ")",
          }}
        >
          <CardContentOptionBar>
            <div>
              {likeSvg({
                className: "like",
                fill: isLike ? "#F24D06" : "none",
                stroke: "white",
                strokeWidth: "2",
                onClick: (item) => this.onClickLike(!isLike),
              })}
              {shareSvg({
                className: "share",
                width: "22px",
                height: "23px",
                stroke: "white",
                strokeWidth: "2",
              })}
            </div>
          </CardContentOptionBar>
          <CardContentDateTimeBar>
            <div className="date">{dateStr}</div>
            <div className="time">{timeStr}</div>
          </CardContentDateTimeBar>
          <CardContentTitleBar>{event_title}</CardContentTitleBar>
          <CardContentBuyTicketButton
            style={{
              fontWeight: isOnMouseOver ? 900 : 400,
            }}
          >
            Buy Ticket
          </CardContentBuyTicketButton>
        </ActivityCardContent>
      </EventCardWrapper>
    );
  }

  onMouseOver(item) {
    this.setState({
      isOnMouseOver: true,
    });
  }

  onMouseOut(item) {
    this.setState({
      isOnMouseOver: false,
    });
  }

  onClickLike(like) {
    this.setState({
      isLike: like,
    });
  }
}
