import React, { memo } from "react";
import KTExpandableButton from "../../../../../../components/expandable-button";
import { Dropdown } from "antd";
import { HomeFilterDateButtonItem } from "./style";

const KTHomeFilterDateButton = memo(() => {
  const switchItems = [
    "All Date",
    "Today",
    "This Weekend",
    "This Moth",
    "Custom",
  ];

  const items = switchItems
    .map((title, index, array) => {
      const resultArr = [
        {
          label: (
            <HomeFilterDateButtonItem className="title">
              {title}
            </HomeFilterDateButtonItem>
          ),
          key: index,
        },
      ];

      if (index < array.length - 1) {
        resultArr.push({
          type: "divider",
        });
      }

      return resultArr;
    })
    .flat();

  return (
    <Dropdown
      menu={{ items }}
      trigger={["click"]}
      overlayStyle={{
        borderRadius: "10px",
        overflow: "hidden",
        border: "1px solid #000",
        background: "#FFF",
        padding: "1px",
      }}
    >
      <KTExpandableButton
        placement="bottomLeft"
        title={"Date Range"}
        onClick={(e) => e.preventDefault()}
      ></KTExpandableButton>
    </Dropdown>
  );
});

export default KTHomeFilterDateButton;
