import React, { memo } from "react";
import KTExpandableButton from "../../../../../../components/expandable-button";
import { Dropdown } from "antd";
import { HomeFilterCategoriesButtonItem } from "./style";

const KTHomeFilterCategoriesButton = memo(() => {
  const switchItems = [
    "NFL",
    "NCAA W",
    "High School Football",
    "NCAA BB",
    "NCAA F",
  ];

  const items = switchItems
    .map((title, index, array) => {
      const resultArr = [
        {
          label: (
            <HomeFilterCategoriesButtonItem className="title">
              {title}
            </HomeFilterCategoriesButtonItem>
          ),
          key: index,
        },
      ];

      if (index < array.length - 1) {
        resultArr.push({
          type: "divider",
        });
      }

      return resultArr;
    })
    .flat();

  return (
    <Dropdown
      menu={{ items }}
      trigger={["click"]}
      overlayStyle={{
        borderRadius: "10px",
        overflow: "hidden",
        border: "1px solid #000",
        background: "#FFF",
        padding: "1px",
      }}
    >
      <KTExpandableButton
        placement="bottomLeft"
        title={"All Categories"}
        onClick={(e) => e.preventDefault()}
      ></KTExpandableButton>
    </Dropdown>
  );
});

export default KTHomeFilterCategoriesButton;
