import { getEvents } from "../../../services/events";
import * as actionTypes from "./constants";

const addHomeEventsAction = (events) => ({
  type: actionTypes.ADD_MORE_EVENTS,
  events: events,
});

const emptyAndAddHomeEventsAction = (events) => ({
  type: actionTypes.EMPTY_ADD_MORE_EVENTS,
  events: events,
});

export const getHomeEventsAction = (
  pageIndex,
  pageSize,
  needEmpty,
  complete
) => {
  return (dispatch) => {
    getEvents().then((res) => {
      const events = res;
      if (needEmpty) {
        dispatch(emptyAndAddHomeEventsAction(events));
      } else {
        dispatch(addHomeEventsAction(events));
      }
    });
  };
};

// 浏览模式切换

const changeHomeLayoutMode = (layoutMode) => ({
  type: actionTypes.CHANGE_LAYOUT_MODE,
  layoutMode: layoutMode,
});

export const getChangeHomeLayoutModeAction = (layoutMode) => {
  return (dispatch) => {
    dispatch(changeHomeLayoutMode(layoutMode));
  };
};
