import React, { memo } from "react";
import { AppFooterBottomWrapper } from "./style";
import { footerBottomLinks } from "../../common/local-data";

const KTFooterBottom = memo(() => {

  const bottomLinks = footerBottomLinks.map((item, index) => {
    if (item.link.length > 0) {
      return <a className="linkItem" key={item.title} href={item.link}>{item.title}</a>
    } else {
      return <div className="linkItem" key={item.title} href={item.link}>{item.title}</div>
    }
  });

  return (
    <AppFooterBottomWrapper>
      <div className="leftWrapper">
        <img
          src={require("../../assets/img/ktlogo-black.png")}
          alt=""
        ></img>
        <div className="allRightReserved">
          © 2023 KUSTOMTICKETS, All rights reserved
        </div>
      </div>
      <div className="rightWrapper">
        <img
          className="privateIcon"
          src={require("../../assets/img/icons/icon-footer-privacy.png")}
          alt=""
        ></img>
        {bottomLinks}
      </div>
    </AppFooterBottomWrapper>
  );
});

export default KTFooterBottom;
